@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap'); /* a nice handwritten font */

.grid-article-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    padding: 20px;
    width: 90%;
    margin: 0 auto;
}

.grid-article-container-3{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    padding: 20px;
    width: 90%;
    margin: 0 auto;
}

.grid-article-item {
    position: relative;
    display: block;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    transition: transform 0.2s, box-shadow 0.2s;
    text-decoration: none;
    color: inherit;
    width: 100%;
    height: auto;
}

.image-wrapper {
    position: relative;
    width: 100%;
    //
}

.image-wrapper img {
    // display: block;
    width: 100%;
    height: auto;
    object-fit: cover;
}

.grid-article-item .note {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50%; /* smaller width */
    background: rgba(0, 0, 0, 0.25); /* more transparent */
    color: white;
    padding: 8px 12px; /* smaller padding */
    border-radius: 12px;
    font-size: 22px;
    font-family: 'Pacifico', cursive;
    text-align: center;
    backdrop-filter: blur(2px); /* smaller blur */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease;
}

//
.grid-article-item:hover .note {
    opacity: 1;
}


.grid-article-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.grid-article-item img {
    display: block;
    height: auto;
    
    width: 100%;
    height: 150px;
    object-fit: cover;
    transition: transform 0.3s, opacity 0.3s;
}
.grid-article-item:hover img {
    transform: scale(1.05);
    opacity: 0.8;
}
.grid-article-item .name {
    margin-top: 10px;
    font-size: 1.2em;
    color: #333;
    padding: 10px;
    text-align: center;
}


.grid-article-item .tiny-logo {
    position: absolute;
    bottom: 5px;
    right: 5px;
    width: 50px; /* adjust size if needed */
    height: auto;
    opacity: 0.8;
    transition: opacity 0.3s ease;
}



.grid-article-item:hover .tiny-logo {
    opacity: 1;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
    .grid-article-container {
        /* grid-template-columns: 1fr; */
        padding: 10px;
    }
    .grid-article-container-3{
        /* grid-template-columns: 1fr; */
        padding: 5px;
        gap: 10px;
    }

    .grid-article-item img {
        height: 200px; /* Set a fixed height */
    }

    .grid-article-item .note {
        font-size: 14px; /* smaller text for note */
        padding: 6px 8px; /* smaller padding */
        width: 60%; /* slightly wider to fit smaller text nicely */
    }

    .grid-article-item .tiny-logo {
        width: 28px; /* smaller logo */
        bottom: 4px;
        right: 4px;
    }
}