.top-articles-container-child {
    padding: 2rem;
    background-color: #fff8f0;
    border-radius: 16px;
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.05);
    //
    max-width: 600px;
    margin: 30px auto;
}

.top-title-container-child {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 2rem;
    background: linear-gradient(90deg, #6a5acd, #00bcd4);
    padding: 1rem 1.5rem;
    border-radius: 12px;
    color: #ffffff;

    svg {
        fill: #ffffff;
        flex-shrink: 0;
    }

    h2.top-title {
        margin: 0;
        font-size: 1.6rem;
        font-weight: 600;
    }
}

.child-escape-card {
    display: flex;
    align-items: flex-start;
    text-decoration: none;
    background: #fff3e0;
    border-radius: 20px;
    padding: 1rem;
    margin-bottom: 1rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
    transition: transform 0.2s, box-shadow 0.2s;
    color: inherit;

    &:hover {
        transform: scale(1.02);
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    }

    &.closed {
        background-color: #ffe0e0;
        opacity: 0.75;

        .child-status {
            color: #c0392b;
            font-weight: bold;
        }
    }
}

.child-rank {
    font-size: 1.5rem;
    color: #ff9800;
    font-weight: bold;
    margin-right: 10px;
    
    display: flex;              /* make the bullet a flexbox */
    align-items: center;        /* center bullet vertically */
    justify-content: center;    /* center horizontally */
    line-height: 1;             /* remove extra text spacing */
    min-width: 1.5rem;          /* optional: keeps spacing consistent */
}


.child-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 12px;
    margin-right: 1rem;
    flex-shrink: 0;
    border: 2px solid #ffd54f;
}

.child-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.child-title {
    font-size: 1.2rem;
    color: #3f51b5;
    margin-bottom: 0.25rem;
    font-weight: 600;
}

.child-enseigne {
    font-size: 1rem;
    color: #5e35b1;
    margin-bottom: 0.5rem;
}

.child-details {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    font-size: 0.9rem;

    span {
        background: #f3e5f5;
        padding: 0.3rem 0.6rem;
        border-radius: 10px;
        color: #4a148c;
    }
}

.child-status {
    margin-top: 0.5rem;
    font-size: 1rem;
}


@media (max-width: 600px) {
  .container {
    padding: 1rem;
  }

  .top-title-container-child {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
    padding: 0.75rem 1rem;
  }

  .top-title-container-child h2.top-title {
    font-size: 1.3rem;
  }

  .child-escape-card {
    flex-direction: column;
    padding: 0.75rem;
  }

  .child-rank {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
    margin-right: 0;
  }

  .child-image {
    width: 80px;
    height: 80px;
    margin-right: 0;
    margin-bottom: 0.75rem;
  }

  .child-info {
    justify-content: flex-start;
  }

  .child-title {
    font-size: 1rem;
  }

  .child-enseigne {
    font-size: 0.9rem;
  }

  .child-details {
    font-size: 0.8rem;
  }

  .child-status {
    font-size: 0.9rem;
  }
}