.translate-btn {
    display: inline-block;
    background-color: #ff6600;
    color: #fff;
    border: none;
    padding: 0.6rem 1.2rem;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 6px;
    cursor: pointer;
    margin-top: 1rem;
    transition: background-color 0.3s ease, transform 0.2s ease;

    &:hover {
      background-color: #e65c00;
      transform: translateY(-1px);
    }

    &:active {
      transform: scale(0.97);
    }
  }