.blackgold-top-container {
  background-color: #000;
  color: #D6A856;
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 0 25px rgba(255, 215, 0, 0.15);
  max-width: 800px;
  margin: 0 auto;
}

.blackgold-top-header {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem; // reduced spacing
  text-align: center;
}

.blackgold-top-header-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blackgold-top-pretitle {
  font-size: 1.1rem;
  color: #ccc;
  margin-bottom: 0.3rem;
}

.blackgold-top-logo {
  height: 90px;
  object-fit: contain;
  margin-bottom: 0.3rem;
}

.blackgold-top-title {
  font-size: 2rem;
  font-weight: bold;
  color: #D6A856;
  margin-top: 0;
}

.blackgold-top-data {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

// Article Card
.blackgold-article-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #111;
  border: 1px solid #333;
  border-radius: 14px;
  padding: 1rem;
  gap: 1rem;
  text-decoration: none;
  color: inherit;
  transition: transform 0.2s ease, box-shadow 0.2s ease;

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 20px rgba(255, 215, 0, 0.2);
  }
}

// Rank
.blackgold-article-rank {
  font-weight: bold;
  font-size: 1.3rem;
  color: #FFD700;
  background-color: #1a1a1a;
  border: 2px solid #D6A856;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  text-align: center;
  line-height: 1;
  span {
    margin-top: 2px;
    display: block;
    font-size: 0.9rem;
  }
}

// Main section
.blackgold-article-main {
  display: flex;
  gap: 1rem;
  flex: 1;
  align-items: center;
}

.blackgold-article-image-wrapper {
  flex-shrink: 0;
  width: 160px;
  height: 100px;
  overflow: hidden;
  border-radius: 10px;
  background-color: #222;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.blackgold-article-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.blackgold-article-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #FFD700;
}

.blackgold-article-enseigne {
  font-size: 0.9rem;
  color: #aaa;
}

// Note and button on far right
.blackgold-note-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.4rem;
  min-width: 90px;
  text-align: right;
}

.blackgold-article-note {
  font-weight: bold;
  color: #D6A856;
  font-size: 1rem;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
}

.blackgold-note-button {
  padding: 0.4rem 0.8rem;
  background-color: #D6A856;
  color: #000;
  border: none;
  border-radius: 6px;
  font-size: 0.85rem;
  cursor: pointer;
  font-weight: 600;
  transition: background 0.3s ease;

  &:hover {
    background-color: #e6c200;
  }
}


@media (max-width: 768px) {
    .blackgold-article-card {
        padding: 0.75rem;
        gap: 0.75rem;
        align-items: center;
    }
    
    .blackgold-article-rank {
        align-self: center; /* ✅ Ensure rank aligns with rest */
    }
    .blackgold-article-main {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.5rem;
        width: 130px; /* limit the width to match the image */
        flex-shrink: 0;
    }

    .blackgold-article-image-wrapper {
        width: 130px;
        height: 85px;
    }

    .blackgold-article-title {
        font-size: 0.95rem;
        line-height: 1.2;
    }

    .blackgold-article-enseigne {
        font-size: 0.8rem;
    }

    .blackgold-note-wrapper {
        align-items: center;
        justify-content: center;
        min-width: 90px;
        flex: 1;
        gap: 0.3rem;
        text-align: right;
    }

    .blackgold-note-button {
        font-size: 0.75rem;
        padding: 0.3rem 0.5rem;
    }

    .blackgold-article-note {
        font-size: 0.9rem;
    }
}