footer {
    width: 100%;
    background: black;
    color: white;
    padding: 20px 0;
}

.row {
    width: 85%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
}

.footer-col {
    flex: 1 1 30%;
    min-width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 10px;
}

.logo-social-group {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
}

.logo-img {
    width: 100px;
    height: auto;
}

.social-icons {
    display: flex;
    gap: 15px;
    align-items: center;
    flex-wrap: wrap;
}

.icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 6px;
    background: white;
    transition: 0.3s;
}

.icon:hover {
    background: #ccc;
}

footer a {
    color: white;
    text-decoration: underline;
}

footer a:hover {
    text-decoration: none;
    color: #aaa;
}


/* Mobile layout */
@media (max-width: 600px) {
    .row {
        flex-direction: column;
        align-items: flex-start;
    }

    .footer-col {
        flex: 1 1 100%;
        text-align: left;
    }

    .social-icons {
        margin-top: 10px;
        margin-bottom: 15px;
    }
}