// 🎃 Horror Theme Colors
$blood-red: #b30000;
$blood-red-dark: #5a0000;
$scare-low: #4caf50;      // green
$scare-mid: #ff9800;      // orange
$scare-high: #f44336;     // bright red
$scare-extreme: #b71c1c;  // dark blood red
$text-light: #f5f5f5;
$shadow-red: rgba(255, 0, 0, 0.4);

//
// 🎃 Main Horror Container
//
.top-articles-container-horror {
  max-width: 600px;
  margin: 30px auto;
  font-family: 'Creepster', Arial, sans-serif;
  padding: 0 15px;
}

//
// 🎃 Title Block
//
.top-title-container-horror {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.2rem;
  background: linear-gradient(90deg, $blood-red, $blood-red-dark);
  padding: 1rem 1.5rem;
  border-radius: 12px;
  color: $text-light;
  box-shadow: 0 0 15px $shadow-red;
}

.top-title-container-horror svg {
  fill: $text-light;
  width: 28px;
  height: 28px;
  margin-right: 12px;
}

.top-title-horror {
  color: $text-light;
  font-size: 1.6em;
  margin: 0;
  text-shadow: 0 0 8px $blood-red;
}

//
// 🎃 Horror Articles
//
.top-articles-placeholder-horreur {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.horreur-article-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(20, 0, 0, 0.95);
  border: 2px solid $blood-red;
  border-radius: 12px;
  padding: 1rem;
  color: $text-light;
  text-decoration: none;
  box-shadow: 0 0 15px $shadow-red;
  transition: transform 0.2s ease, box-shadow 0.2s ease;

  &:hover {
    transform: scale(1.02);
    box-shadow: 0 0 25px $blood-red;
    background: rgba(40, 0, 0, 0.95);
  }

  // Left: Rank
  .horreur-article-rank {
    font-size: 1.4rem;
    font-weight: bold;
    color: $blood-red;
    margin-right: 1rem;
    // text-shadow: 0 0 6px $blood-red;
    flex-shrink: 0;
    min-width: 40px;
    text-align: center;
  }

  // Middle: Image + Content
  .horreur-article-main {
    display: flex;
    align-items: center;
    flex: 1;
    gap: 1rem;

    .horreur-article-image-wrapper {
      flex-shrink: 0;

      img {
        width: 80px;
        height: 80px;
        object-fit: cover;
        border-radius: 8px;
        border: 2px solid $blood-red;
        box-shadow: 0 0 8px $blood-red;
      }
    }

    .horreur-article-content {
      display: flex;
      flex-direction: column;
      justify-content: center;

      .horreur-article-title {
        font-size: 1.1rem;
        font-weight: bold;
        margin-bottom: 0.2rem;
        color: $text-light;
      }

      .horreur-article-enseigne {
        font-size: 0.85rem;
        color: lighten($blood-red, 20%);
      }
    }
  }

  // Right: Scaremeter
  .horreur-scaremeter {
    margin-left: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.3rem;

    .scaremeter-label {
      font-size: 1rem;
      color: $blood-red;
    //   text-shadow: 0 0 4px $blood-red;
      font-weight: bold;
      text-align: center;
    }

    .scaremeter-thermo {
      width: 18px;
      height: 90px;
      background: #111;
      border: 2px solid $blood-red;
      border-radius: 8px;
      overflow: hidden;
      display: flex;
      align-items: flex-end;
      position: relative;

      // Tick marks
      &::before {
        content: "";
        position: absolute;
        top: 25%;
        left: 0;
        width: 100%;
        height: 2px;
        background: rgba(255, 255, 255, 0.15);
      }
      &::after {
        content: "";
        position: absolute;
        top: 60%;
        left: 0;
        width: 100%;
        height: 2px;
        background: rgba(255, 255, 255, 0.15);
      }

      .scaremeter-fill {
        width: 100%;
        transition: height 0.3s ease-in-out;
        background: linear-gradient(
          to top,
          $scare-low 0%,
          $scare-mid 40%,
          $scare-high 70%,
          $scare-extreme 100%
        );
        box-shadow: 0 0 10px $blood-red;
      }
    }
  }
}

//
// 📱 Mobile Layout (keep horizontal)
//
@media (max-width: 600px) {
  .horreur-article-card {
    flex-direction: row;   // ✅ keep horizontal
    align-items: center;
    text-align: left;
    padding: 0.8rem;

    .horreur-article-rank {
      font-size: 1.2rem;
      margin-right: 0.6rem;
    }

    .horreur-article-main {
      flex: 1;
      gap: 0.6rem;

      .horreur-article-image-wrapper img {
        width: 65px;
        height: 65px;
      }

      .horreur-article-content {
        .horreur-article-title {
          font-size: 0.95rem;
        }
        .horreur-article-enseigne {
          font-size: 0.75rem;
        }
      }
    }

    .horreur-scaremeter {
      margin-left: 0.6rem;

      .scaremeter-thermo {
        width: 14px;
        height: 70px;
      }

      .scaremeter-label {
        font-size: 0.65rem;
      }
    }
  }
}
