.immersive-block {
  max-width: 900px;
  margin: 0 auto 3rem auto;
  padding: 0 1.5rem;
  font-family: 'Segoe UI', sans-serif;
  line-height: 1.6;

  h2 {
    font-size: 2rem;
    color: #111;
    margin-bottom: 1rem;
    font-weight: 700;
    border-left: 5px solid #0b0b0b;
    padding-left: 0.75rem;
  }

  h3 {
    font-size: 2rem;
    color: #111;
    margin-bottom: 1rem;
    font-weight: 700;
    text-align: center;
  }

  h5 {
    font-size: 1.125rem;
    font-weight: 400;
    color: #333;
    background: #f9f9f9;
    padding: 1rem 1.25rem;
    border-radius: 8px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
    margin-top: 0;
    margin-bottom: 1.5rem;
  }

  .description {
    background-color: #ffffff;
    padding: 1rem 1.25rem;
    border-left: 4px solid #ddd;
    border-radius: 6px;
    margin-bottom: 2rem;

    p {
      margin-bottom: 1rem;
      color: #444;
      font-size: 1rem;

      strong {
        font-weight: 700;
        color: #000;
      }

      br {
        display: block;
        margin: 0.5rem 0;
      }
    }
  }


  a.styled-link {
    color: #ff6600;
    font-weight: 600;
    text-decoration: none;
    border-bottom: 1px dashed #ff6600;
    transition: all 0.2s ease-in-out;

    &:hover {
      color: #e65c00;
      border-bottom: 1px solid #e65c00;
    }
  }
}

@media (max-width: 768px) {
  .immersive-block {
    padding: 0 1rem;

    h2 {
      font-size: 1.5rem;
    }

    h5 {
      font-size: 1rem;
      padding: 0.875rem 1rem;
    }

    .description {
      padding: 0.875rem 1rem;

      p {
        font-size: 0.95rem;
      }
    }

  }
}
